import React from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import SkillsTab from "./SkillsTab";
import { Jumbotron } from "./migration";
import { useScrollPosition } from "../../hooks/useScrollPosition";
import { Icon } from "@iconify/react";
import { Fragment } from "react";
import { Fade } from "react-reveal";
import { Col, Container, Row, UncontrolledTooltip } from "reactstrap";
import {Dimensions} from "react-native";
import { useWindowDimensions } from 'react-native';


const Skills = React.forwardRef(({ heading, data }, ref) => {
	var { height, width } = useWindowDimensions();
	width = parseInt(width/15);
  const skillsTabRef = React.useRef(null);
  const [isScrolled, setIsScrolled] = React.useState(false);
  //const navbarDimensions = useResizeObserver(navbarMenuRef);


  useScrollPosition(
    ({ prevPos, currPos }) => {
      if (!isScrolled && currPos.y - 400 < 0) setIsScrolled(true);
    },
    [],
    skillsTabRef
  );
  return (
 
    <Jumbotron ref={skillsTabRef} fluid className="bg-white m-0" id="skills">
      <Container className="p-5 ">
        <h2 ref={skillsTabRef} className="display-4 pb-5 text-center">
          {heading}
        </h2>
        	{data.map((section, index) => {
				return (
					<Row className="my-12 center" key={index}>
						<Col lg="12" className="order-1 order-lg-2">
							<Fade right duration={2000}>
								<h3 className="h3 mb-2 d-flex justify-content-center">{section.title}</h3>
								<div className="d-flex justify-content-center flex-wrap mb-2">
									{section.softwareSkills.map((skill, i) => {
										return (
											<Fragment key={i}>
												<div
													
													id={skill.skillName.replace(/\s/g, '')}
												
												>

												<img className="icon icon-lg icon-shape shadow-sm rounded-circle m-1" src={skill.skillImage} width={width} height={width} alt="" />

												</div>
												<UncontrolledTooltip
													delay={0}
													placement="bottom"
													target={skill.skillName.replace(/\s/g, '')}
												>
													{skill.skillName}
												</UncontrolledTooltip>
											</Fragment>
										);
									})}
								</div>
								<div >
									{section.skills.map((skill, i) => {
										return <p className="d-flex justify-content-center flex-wrap mb-2" key={i}>{skill}</p>;
									})}
								</div>
							</Fade>
						</Col>
					</Row>
				);
			})}
      </Container>
    </Jumbotron>
  );
});

export default Skills;
